export function friendlyDate(dataStr){
    let dataObj = typeof dataStr === 'object' ? dataStr : new Date(dataStr)
    let time = dataObj.getTime()
    let now = Date.now()
    let space = now - time
    let str = '' 

    switch(true){
        case space < 60*1000:
            str='刚刚'
            break
        case space < 60*1000*60:
            str=Math.floor(space/(60*1000))+'分钟前'
            break
        case space < 60*1000*60*24:
            str=Math.floor(space/(60*1000*60))+'小时前'
            break
        default:
            str=Math.floor(space/(60*1000*60*24))+'天前'
    }
    return str
}


