<template>
    <span :title="username">{{slug}}</span>
</template>

<script>
    import Auth from '@/apis/auth'
    // import Bus from '@/helpers/bus'
    import {mapActions,mapGetters} from 'vuex'

    export default {
        name:'Avatar',
        data(){
            return {}
        },

        /* 在这个生命周期获取数据，这个时候已经有data了 */
        created(){
           /*  Bus.$on('userInfo',user=>{
                this.username = user.username
            }) */
            /* Auth.getInfo()
                .then(res=>{
                    if(res.isLogin){
                        this.username = res.data.username
                        // console.log(res)
                    }
                }) */
            this.setUser({path:'/'}) // 这里不是检验他的是否登录，而是为了把那个头像设置上去
            // this.setUser()  // 这是课上写的
        },

        methods:{
            ...mapActions(['setUser'])
            /* ...mapActions({'setUser':checkLogin}) */   // 这个注释的地方是老师写的
        },

        computed:{
            ...mapGetters([
                'username',
                'slug'
            ]),
        }
    }
</script>

<style scoped>
    span {
        display: inline-block;
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 32px;
        border-radius: 50%;
        background: #f2b81c;
        color: #fff;
        text-shadow: 1px 0 1px #795c19;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 18px;
        margin-top: 15px;
}
</style>


