import request from '@/helpers/request'
import {friendlyDate} from '@/helpers/util'

const URL = {
    GET:'/notebooks',
    ADD:'/notebooks',
    UPDATE:'/notebooks/:id',
    DELETE:'/notebooks/:id'
}

export default {
    getAll(){
        return new Promise((resolve,reject)=>{
            request(URL.GET)
                .then(res=>{
                    // 很奇怪，我这里排序不太生效
                    // res.data.forEach(notebook=>{console.log(notebook.createdAt,typeof(notebook.createdAt))})
                    res.data = res.data.sort(function(notebook1, notebook2) {
                        if(notebook1.createdAt > notebook2.createdAt){
                            return -1
                        }
                        if(notebook1.createdAt < notebook2.createdAt){
                            return 1
                        }
                        return 0
                    })
                    res.data.forEach(notebook=>{
                        // console.log(notebook.createdAt)   // 看看上面排序输出
                        notebook.createdAtFriendly = friendlyDate(notebook.createdAt)
                        notebook.updatedAtFriendly = friendlyDate(notebook.updatedAt)
                    })  
                    // console.log(res.data)
                    resolve(res)
                }).catch(err=>{
                    // console.log(err)
                    reject(err)
                })
        })
    },

    updateNotebook(notebookId,{title=''}={title:''}){
        return request(URL.UPDATE.replace(':id',notebookId),'PATCH',{title})
    },

    deleteNotebook(notebookId){
        return request(URL.DELETE.replace(':id',notebookId),'DELETE')
    },

    addNotebook({title=''}={title:''}){
        return new Promise((resolve,reject)=>{
            request(URL.ADD,'POST',{title})
                .then(res=>{
                    res.data.createdAtFriendly = friendlyDate(res.data.createdAt)
                    res.data.updatedAtFriendly = friendlyDate(res.data.updatedAt)
                    resolve(res)
                }).catch(err=>{
                    reject(err)
                })
        })
         
    }
}


