import Auth from '@/apis/auth'
import router from '@/router'

window.router = router
const state = {
  user:null
}

const getters = {
  username:state => state.user === null ? '未登录' : state.user.username,

  slug:state => state.user === null ? '未' : state.user.username.charAt(0)
}

const mutations = {
  setUser(state,payload){
    state.user = payload.user
  }
}

const actions = {
  login({commit},{username,password}){
    return Auth.login({username,password})
      .then(res=>{
        // console.log(res)   // 看看服务器返回来给我们的就是我们输入的，然后把这个赋给user
        commit('setUser',{user:res.data})
      })
  },

  register({commit},{username,password}){
    return Auth.register({username,password})
      .then(res=>{
        // console.log(res)
        commit('setUser',{user:res.data})
      })
  },

  logout({commit},payload = {path:'/login'}){
    return Auth.logout()
      .then(res=>{
        commit('setUser',{user:null})
        router.push(payload)
      })
  },

  checkLogin({commit,state},payload){
    if(state.user !== null) return Promise.resolve()
    return Auth.getInfo()
      .then(res=>{
        if(!res.isLogin){
          console.log('jump')
          console.log(payload)
          router.push(payload)
        } else{
          commit('setUser',{user:res.data})
        }
      })
  },

  setUser({commit}){
    return Auth.getInfo()
      .then(res=>{
        if(!res.isLogin){
          console.log('noJump')
        } else{
          commit('setUser',{user:res.data})
        }
      })
  }
}


export default {
  state,
  getters,
  mutations,
  actions
}


